            let resultOptions = [{ value: { title: '', link: ''}, text: "ÐÐµÐ· Ð¿Ð¾Ð´ÑÑÐ±ÑÐ¸ÐºÐ¸" }];
            //console.log(categoryLink);
            if (choosedCategory) {
                const category = this.categories.find(category => category.link === choosedCategory.link);
