                const formData = new FormData();
                const data = JSON.stringify({
                    ...this.newPost,
                    categories: {
                        ...this.newPost.category,
                        subcategory: this.newPost.subcategory,
                    },
                    
                    author: this.thisUser,
                    date: Date.now()
