        validatePost() {
            let errMessage = '';
            const post = this.newPost;
            !post.title ? errMessage+= 'ÐÐ²ÐµÐ´Ð¸ÑÐµ Ð·Ð°Ð³Ð¾Ð»Ð¾Ð²Ð¾Ðº Ð½Ð¾Ð²Ð¾ÑÑÐ¸!\n' : null;
            !post.picture ? errMessage+= 'ÐÐ¾Ð±Ð°Ð²ÑÑÐµ Ð¾Ð±Ð»Ð¾Ð¶ÐºÑ Ð½Ð¾Ð²Ð¾ÑÑÐ¸!\n' : null;
            !post.HTML ? errMessage+= 'ÐÐ²ÐµÐ´Ð¸ÑÐµ ÑÐ¾Ð´ÐµÑÐ¶Ð¸Ð¼Ð¾Ðµ Ð½Ð¾Ð²Ð¾ÑÑÐ¸!\n' : null;
            !post.category ? errMessage+= 'Ð£ÐºÐ°Ð¶Ð¸ÑÐµ ÑÑÐ±ÑÐ¸ÐºÑ Ð½Ð¾Ð²Ð¾ÑÑÐ¸!\n' : null;
            !post.shortcat ? errMessage+= 'ÐÐ¾Ð±Ð°Ð²ÑÑÐµ ÑÐ¾ÑÑÐºÐ°Ñ Ð½Ð¾Ð²Ð¾ÑÑÐ¸!\n' : null;
            if (errMessage) {
