        // ÐÑÐ»Ð¸ Ð½Ð°Ð¹Ð´ÐµÐ½Ð¾ ÑÐ¾Ð²Ð¿Ð°Ð´ÐµÐ½Ð¸Ðµ, ÑÐ¾ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÑÑÑ ÑÐ¾Ð¾ÑÐ²ÐµÑÑÑÐ²Ð¸Ðµ, ÐµÑÐ»Ð¸ Ð½ÐµÑ - Ð¿ÑÑÑÐ°Ñ ÑÑÑÐ¾ÐºÐ°
        newStr += newCh;
